import React from 'react'
import { Link } from 'gatsby';
import Helmet from 'react-helmet';

import Layout from '../components/layout';
import PageNavigation from '../components/page-navigation';

const WrongAnswer = () => {
	return (
		<Layout>
			<section id="header">
				<div className="inner">
					<h1>なにか違うみたい</h1>
				</div>
			</section>
			<Helmet title="不正解 | 氷蔵姉妹の挑戦" />
			<section id="one" className="main style1">
				<div className="grid-wrapper">
					<div className="col-12">
						<p>
							どうやら答えが違うようです。<br />
              全部ひらがなで入力しましたでしょうか？<br />
              もしそうであれば、答えを導く過程のどこかで間違いがあったのかも。<br />
              もしわからなければ、<Link to="/hint/">ヒント</Link> も参考にしてくださいね。
            </p>
					</div>
				</div>
				<div className="grid-wrapper">
					<div className="col-12">
						<PageNavigation prev="/question/" />
					</div>
				</div>
			</section>
		</Layout>
	);
};

export default WrongAnswer;
